import Vue from 'vue'
import Vuex from 'vuex'
import mutationTypes from './mutation-types'
import { listTypes } from '../shared/constants'
import { initBaseData, refreshState } from './mutations'


Vue.use(Vuex)


const state = {

  uid: '', // ID

  /**
   * 
   */
  friends: {}, // 
  groups: {}, // 
  requests: {}, // 
  invitations: {}, // 

  /**
   * 
   * 
   */
  groupDetails: {},

  userBaseInfoMap: {}, // 
  groupBaseInfoMap: {}, // 
  sessionMap: {}, // 
  enterContentMap: {}, // 

  localData: {}, // 

  filter: '', // 

  currentList: listTypes.SESSION_LIST, // 

  currentFriend: '', // 
  currentGroup: '', // 
  currentSession: '', // 
}


const mutations = {

  // 
  [mutationTypes.INIT_BASE_DATA]: initBaseData,

  // state
  [mutationTypes.REFRESH_STATE]: refreshState,
}


/**
 * store
 */
export default new Vuex.Store({
  state,
  mutations,
})



